/* chat */
.chat-answer {
    position: fixed;
    top: 60px;
    left: 0px;
    right: 0px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 60px);
    /* background: var(--main-glass-color); */
    /* backdrop-filter: var(--backdrop-filter); */
    animation: animate-popDown .1s ease-in;
}

.chat-answer-content {
    max-height: calc(100% - 20px - 10px - 20px - 90px);
    background: var(--main-bg-color-2);
    border-radius: var(--border-radius);
    padding: 10px;
    overflow: auto;
    font-size: var(--font-size-mid);
    box-shadow: var(--box-active-shadow);
}

.chat-ask-text {
    height: 40px;
    line-height: 40px;
    width: 100%;
    color: var(--primary-color);
    font-weight: bold;
    overflow-x: auto;
    white-space: nowrap;
}

.chat-answer-text {
    width: 100%;
    height: calc(100% - 40px);
    overflow: auto;
}

/* feature */
.feature-list {
    width: calc(100% - 30px);
    padding: 15px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow: auto;
    padding-bottom: calc(80px + 90px);
}

.feature-item-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.feature-mini-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: calc(100% - 5px);
    height: 100px;
    border-radius: var(--border-radius);
}

.feature-small-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: calc(50% - 5px);
    height: 100px;
    border-radius: var(--border-radius);
}

.feature-mid-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    height: 150px;
    border-radius: var(--border-radius);
}

.feature-larger-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    height: 300px;
    border-radius: var(--border-radius);
}

/* todo */
.todo {
    height: calc(100% - 20px);
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.todo-head {
    height: 35px;
    width: 100%;
    color: var(--primary-color);
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.todo-main {
    height: calc(100% - 35px);
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow: auto;
}

.todo-item {
    height: calc(100% - 10px);
    width: 65px;
    flex-shrink: 0;
    background: var(--primary-color);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: column;
    color: white;
    padding: 5px;
}

.todo-item-title {
    height: 65%;
    width: 100%;
    font-weight: bold;
    word-break: break-all;
    overflow: auto;
    text-align: center;
}

.todo-item-subtitle {
    height: 10%;
    width: 100%;
    font-size: var(--font-size-mini);
}

.todo-item-time {
    height: 25%;
    width: 100%;
    color: var(--warn-color);
}

/* chat-list */
.chat-record-head {
    width: calc(100% - 20px);
    height: 30px;
    line-height: 30px;
    display: flex;
    padding: 5px 10px;
}

.chat-record-head-title {
    width: calc(100% - 30px);
    font-weight: bold;
    color: var(--primary-color);
}

.chat-record-head-operate {
    width: 30px;
    color: var(--danger-color);
    cursor: pointer;
}

.chat-record-list {
    width: calc(100% - 20px);
    height: calc(100% - 40px - 20px);
    display: flex;
    align-items: center;
    margin: 10px;
    gap: 10px;
    color: white;
    overflow: auto;
}

.chat-record-list-item {
    flex-shrink: 0;
    height: 50px;
}
.chat-record-list-content {
    height: 100%;
    padding: 0 10px;
    background: var(--primary-color);
    border-radius: var(--border-radius);
    font-weight: bold;
    cursor: pointer;
}
.chat-record-list-content-del {
    color: red;
    margin-left: 10px;
}